.sg-grid-demo {
	font-family: $secondary-font-stack;
	font-size: 14px;

	.demo-box {
		overflow: hidden;
	}

	.row {
		background: rgba(213, 238, 251, 0.8);
	}

	.row [class^='col-'] {
		padding: $gutter-width;
	}
	.row [class^='col-'] div {
		background: rgba(168, 203, 221, 0.8);
		padding: 5px;
		text-align: center;
	}
}
